module.exports = "<h1 id=\"vcl-icogram\">vcl-icogram</h1>\n<p>Combination of icon and text of which both are optional and can be permuted.<br>Icons can be prepended or appended to a textual label and can be sourced from icon<br>fonts or directly from file based imagery.<br>The component takes care of accessibility aspects such rendering appropriate aria<br>attributes.<br>It also renders accessability labels for icons if no label for the icogram is<br>provided.</p>\n<h2 id=\"usage-\">Usage:</h2>\n<pre class=\"hljs\"><span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icogram</span> <span class=\"hljs-attr\">label</span>=<span class=\"hljs-string\">&quot;some label&quot;</span> <span class=\"hljs-attr\">prepIcon</span>=<span class=\"hljs-string\">&quot;fa:chevron-right&quot;</span> <span class=\"hljs-attr\">flexLabel</span>=<span class=\"hljs-string\">true</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icogram</span>&gt;</span>\n\n<span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icogram</span>&gt;</span>\n  <span class=\"hljs-tag\">&lt;<span class=\"hljs-name\">vcl-icon</span> <span class=\"hljs-attr\">icon</span>=<span class=\"hljs-string\">&quot;fa:close&quot;</span>&gt;</span><span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icon</span>&gt;</span>\n<span class=\"hljs-tag\">&lt;/<span class=\"hljs-name\">vcl-icogram</span>&gt;</span>\n</pre>\n<h3 id=\"api\">API</h3>\n<h4 id=\"properties-\">Properties:</h4>\n<table>\n<thead>\n<tr>\n<th>Name</th>\n<th>Type</th>\n<th>Default</th>\n<th>Description</th>\n</tr>\n</thead>\n<tbody>\n<tr>\n<td><code>label</code> <em>(1)</em></td>\n<td>string</td>\n<td></td>\n<td>The textual label </td>\n</tr>\n<tr>\n<td><code>flexLabel</code></td>\n<td>boolean</td>\n<td></td>\n<td>the label gets a <code>vclLayoutFlex</code> class if true</td>\n</tr>\n<tr>\n<td><code>prepIcon</code></td>\n<td>string</td>\n<td></td>\n<td>Icon as defined by the icon component</td>\n</tr>\n<tr>\n<td><code>appIcon</code></td>\n<td>string</td>\n<td></td>\n<td>Same as <code>prepIcon</code> but appended</td>\n</tr>\n<tr>\n<td><code>prepIconSrc</code></td>\n<td>string</td>\n<td></td>\n<td>Image as defined by the icon component</td>\n</tr>\n<tr>\n<td><code>appIconSrc</code></td>\n<td>string</td>\n<td></td>\n<td>Same as <code>prepIconSrc</code> but appended</td>\n</tr>\n</tbody>\n</table>\n<p><em>(1) Supports l10n</em></p>\n"

